import React from "react";
import "../dist/css/main.css";
import faq from "../local-json/local-faq";
import socialmedia from "../local-json/local-social-media";
import Blog from "../components/blog";
import SocialMedia from "../components/socialMedia";

var blogComponent = faq.map(element => <Blog data={element}></Blog>);
var socialMediaComponent = socialmedia.map(element => (
    <SocialMedia data={element}></SocialMedia>
));

const Faq = () => {
    return (
        <div className="faq__container">
            <div className="faq__headline">
                <h1 className="faq__headline--title">How can we help you?</h1>
            </div>
            <div className="">{blogComponent}</div>
            <div className="faq__icons--box sewcase-primary-light">
                <h3 className="faq__headline--no-margin">
                    About all Icons and patterns used on this page
                </h3>
                <p>they were made by</p>
                <div className="faq__socialmedia">
                    <a
                        href="https://www.flaticon.com/authors/vitaly-gorbachev"
                        className="faq__icons--title"
                    >
                        Vitaly Gorbachev
                    </a>
                    <a
                        href="https://www.flaticon.com/authors/pixel-perfect"
                        className="faq__icons--title"
                    >
                        Pixel perfect
                    </a>
                    <a
                        href="https://www.flaticon.com/authors/freepik"
                        className="faq__icons--title"
                    >
                        Freepik
                    </a>
                    <a
                        href="https://www.freepik.com/free-photos-vectors/background"
                        className="faq__icons--title"
                    >
                        starline
                    </a>
                    <a
                        href="https://www.freepik.com/free-photos-vectors/background"
                        className="faq__icons--title"
                    >
                        Harryarts
                    </a>
                </div>
            </div>
            <div className="faq__icons--box sewcase-primary">
                <p>You can also contact us on :</p>
                <div className="faq__social-media">{socialMediaComponent}</div>
            </div>
        </div>
    );
};

export default Faq;
