import React from "react";
import "../dist/css/main.css";
import { Link } from "react-router-dom";

const linkArray = [
    { path: "/", linkText: "About Sewcase" },
    { path: "/convention", linkText: "Convention" },
    { path: "/organizers", linkText: "Organizers" },
    { path: "/faq", linkText: "FAQ" }
];


var toggleBurgerMenu = () => {
    var navIcon = document.getElementById("nav-icon");
    var navBar = document.getElementById("nav-bar");
    navIcon.classList.toggle("burger-menu--open");
    navBar.classList.toggle("header-mobile__navigation--open");
};

const Header = () => {
    return (
        <>
            <nav className="header-mobile">
                <div id="nav-icon" onClick={toggleBurgerMenu}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
                <div id="nav-bar" className="header-mobile__navigation">
                    <ul className="header-mobile__list">
                        {linkArray.map((element, index) => {
                            return (
                                <li key={index} className="header-mobile__list--item">
                                    <Link 
                                        onClick={toggleBurgerMenu}
                                        to={element.path}
                                        className="header-mobile__link"
                                    >
                                        {element.linkText}
                                    </Link>
                                </li>
                            );
                        })}
                    </ul>
                </div>
            </nav>
            <nav className="header">
                <div className="header__navigation">
                    <h2 className="header__info-text">
                        Ein Projekt von und für Cosplayer
                    </h2>
                    <section className="header__section">
                        <ul className="header__upper-list">
                            {linkArray.map(element => {
                                return (
                                    <li>
                                        <Link
                                            to={element.path}
                                            className="header__link"
                                        >
                                            {element.linkText}
                                        </Link>
                                    </li>
                                );
                            })}
                        </ul>
                    </section>
                </div>
            </nav>
        </>
    );
};

export default Header;
