import React from "react";
import "../dist/css/main.css";

var SocialMedia = obj => {
    return (
        <div className="social-media__box">
            <a href={obj.data.href}>
                <img
                    src={require("../dist/images/" + obj.data.image + ".png")}
                    className="social-media__box--image"
                    alt=""
                />
            </a>
            <p>{obj.data.title}</p>
        </div>
    );
};

export default SocialMedia;
