import React from "react";
import "../dist/css/main.css";

var Card = obj => {
    var leftCardStyle = "left-box " + obj.data.cardLeft;
    var rightCardStyle = "right-box " + obj.data.cardRight;
    return (
        <div className="card-container">
            <div className={leftCardStyle}>
                <div className="right-box__headline">{obj.data.nameBack}</div>
                <div className="right-box__content">{obj.data.infoBack}</div>
            </div>
            <div className="middle-box">
                <div className="middle-box__category">{obj.data.job}</div>
                <div className="middle-box__image">
                    <img
                        src={require("../dist/images/" +
                            obj.data.image +
                            ".png")}
                        className="middle-box__image--size"
                        alt=""
                    />
                </div>
                <div className="middle-box__category">{obj.data.date}</div>
            </div>
            <div className={rightCardStyle}>
                <div className="right-box__content">{obj.data.info}</div>
                <div className="right-box__headline">— {obj.data.name}</div>
            </div>
        </div>
    );
};

export default Card;
