import React from "react";
import "../dist/css/main.css";

var ConventionCard = obj => {
    var imageArray = obj.data.imageArr;
    return (
        <div className="convention-box">
            <div class="convention-box__image">
                {imageArray.map(element => {
                    return (
                        <img
                            src={require("../dist/images/" + element.image)}
                            className=""
                            alt=""
                        />
                    );
                })}
            </div>
            <div class="convention-box__content">
                <div class="convention__nav-box">
                    <h2>{obj.data.title}</h2>
                    <a href="#" class="convention__nav-box__link">
                        Zu den Bildern
                    </a>
                </div>
            </div>
        </div>
    );
};

export default ConventionCard;
