import React from "react";
import "../dist/css/main.css";
import ConventionCard from "../components/conventionCard";

var conventionArray = [
    {
        imageArr: [
            { image: "gamescom-team-edited.jpg" },
            { image: "first.jpg" },
            {
                image:
                    "146-1466338_poro-smirk-emoji-league-of-legends-discord.png"
            }
        ],
        title: "Gamescom 2019"
    },
    {
        imageArr: [
            { image: "first.jpg" },
            {
                image:
                    "146-1466338_poro-smirk-emoji-league-of-legends-discord.png"
            },
            { image: "gamescom-team-edited.jpg" }
        ],
        title: "Gamescom 2018"
    },
    {
        imageArr: [
            {
                image:
                    "146-1466338_poro-smirk-emoji-league-of-legends-discord.png"
            },
            { image: "gamescom-team-edited.jpg" },
            { image: "first.jpg" }
        ],
        title: "Gamescom 2017"
    }
];

var conventionCards = conventionArray.map(element => (
    <ConventionCard data={element}></ConventionCard>
));

const Convention = () => {
    return <div className="convention__container">{conventionCards}</div>;
};

export default Convention;
